
.block_title{
    text-align: center;
    width: 100%;
}
.block_box{
    width: 100%;
    text-align: center;
    margin-top: 50px;
}
.block_css{
    outline: none;
    width: 300px;
    height: 50px;
    background: rgb(252,100,100);
    border-radius:15px;
    font-size: 20px;
    font-weight: bolder;
    color: white;
}
.clock{
      position:relative;
      font-size:54px;
      margin:64px auto 40px;
      border:8px solid #ccc;
      height:200px;
      width:200px;
      border-radius: 109px;
      -webkit-box-shadow: 0 0 0 10px #ff6666;
              box-shadow: 0 0 0 10px #ff6666;
}
.pie{  
      top:0;
      left:0;
      position:absolute;  
      width:184px;  
      height:184px;  
      border-radius:50%;  
      clip: rect(0px, 92px, 184px, 0px);
      -webkit-transform:rotate(0deg);
              transform:rotate(0deg);
}
.bg {
     background:#F08080;
}
.zindex{
     z-index:1;
}
.clock .timenum{
    position:absolute;
    z-index:2;
    text-align: center;
    line-height:184px;
    left:25px;
}
